<template>
  <div :class="className" :style="{height:height,width:width}"/>
</template>

<script>
import echarts from 'echarts'
require('echarts/theme/macarons') // echarts theme
import { debounce } from '@/utils'
import { getStatistics } from '../../../../api/api'

const animationDuration = 6000

export default {
  props: {
    className: {
      type: String,
      default: 'chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '300px'
    }
  },
  data() {
    return {
      chart: null,
	  listLoading: false,
	  event_distri_1: [],
	  event_distri_2: [],
    }
  },
  created() {
  	this.getParam();
  },
  mounted() {
	this.getParam();
    this.initChart()
    this.__resizeHandler = debounce(() => {
      if (this.chart) {
        this.chart.resize()
      }
    }, 100)
    window.addEventListener('resize', this.__resizeHandler)
  },
  beforeDestroy() {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.__resizeHandler)
    this.chart.dispose()
    this.chart = null
  },
  methods: {
	getParam() {
		let para = {
			api_version: "1.0"
		};
		console.log(para)
		this.listLoading = true;
		getStatistics(para).then((res) => {
			// console.log(res.data);
			this.event_distri_1 = res.data.event_distri_1;
			this.event_distri_2 = res.data.event_distri_2;
			this.listLoading = false;
			// console.log(this.event_distri_2);
		});
	},
    initChart() {
      this.chart = echarts.init(this.$el, 'macarons')

      this.chart.setOption({
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          top: 10,
          left: '2%',
          right: '2%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
		  // data: this.event_distri_1,
          axisTick: {
            alignWithLabel: true
          }
        }],
        yAxis: [{
          type: 'value',
          axisTick: {
            show: false
          }
        }],
        series: [{
          name: 'pageA',
          type: 'bar',
          stack: 'vistors',
          barWidth: '60%',
          data: [79, 52, 200, 334, 390, 330, 220],
		  // data: [this.event_distri_2[0], this.event_distri_2[1], this.event_distri_2[2], this.event_distri_2[3], this.event_distri_2[4], this.event_distri_2[5], this.event_distri_2[6]],
    //       animationDuration
        }, 
		// {
  //         name: 'pageB',
  //         type: 'bar',
  //         stack: 'vistors',
  //         barWidth: '60%',
  //         data: [80, 52, 200, 334, 390, 330, 220],
  //         animationDuration
  //       }, {
  //         name: 'pageC',
  //         type: 'bar',
  //         stack: 'vistors',
  //         barWidth: '60%',
  //         data: [30, 52, 200, 334, 390, 330, 220],
  //         animationDuration
  //       },
		]
      })
    }
  }
}
</script>



